import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom'
import { allProductOfUser, getUser, editUser } from './../../action/action';
import './cart.scss';
import $ from 'jquery'
class Index extends React.Component {
    constructor(props) {
        super(props);
        this.state= {
            productOfUser : {
                allProduct : JSON.parse(localStorage.getItem('productOfUser'))
            }
        }
        this.props.getUser();    
    }
    
    componentWillMount() {

        const productCurrent =JSON.parse( localStorage.getItem('productOfUser'))
        this.setState({
            productOfUser : {
                allProduct : productCurrent
            }
        })
    }
    
    deleteProduct = (event) => {
        let selectProduct = event.currentTarget.parentElement.parentElement.children[1].innerHTML;
        let productRest = JSON.parse(localStorage.getItem('productOfUser')).filter((val) => {
            
            return val.name !== selectProduct;
        })
        this.setState({
            productOfUser : {
                allProduct : productRest
            }
        })
        localStorage.setItem('productOfUser',JSON.stringify(productRest))
        const userLogin = JSON.parse(localStorage.getItem('profileUser'));
        
        this.props.propsProductOfUser({allProduct : null},userLogin.id)
        this.props.propsProductOfUser({allProduct : productRest},userLogin.id)
    }

    removeCart =() => {
        this.setState({
            productOfUser : {
                allProduct : null
            }
        })
        localStorage.removeItem('productOfUser');
        const userLogin = JSON.parse(localStorage.getItem('profileUser'));
        if (userLogin) {
            this.props.propsProductOfUser({allProduct : null},userLogin.id)
            let userLocal = Object.assign({},userLogin,{allProduct : null})
            localStorage.setItem('profileUser',JSON.stringify(userLocal))
        }   
    }

    paypal = () => {
        const userLogin = JSON.parse(localStorage.getItem('profileUser'));
        if (!userLogin) {
            alert('Bạn phải đăng nhập để thanh toán !')
            window.location.href = '/login'
        } else {
            if (!userLogin.allProduct) {
                alert('Bạn chưa đặt hàng!')
            
            } else {
                localStorage.removeItem('productOfUser')
                let totalPay = $('.totalPay').text();
            let infoPay = {
                totalPay,
                statusPay : true,
                allProduct : null,
                historyPay : userLogin.allProduct 
            }
            this.props.updateUser(infoPay,userLogin.id)
            const userLoginNew = Object.assign({},userLogin,infoPay)
            localStorage.setItem('profileUser',JSON.stringify(userLoginNew))
            }

        }
        
        
    }
    
    render() {
        const userLogin = JSON.parse(localStorage.getItem('profileUser'));
        let nhat = '';
       
        if (userLogin) {
            if (!userLogin.allProduct) {
                if (userLogin.historyPay) {
                    nhat = (
                        <Link  style={buttonPaypal} to='/paypal'>
                                <button className="text-uppercase" onClick={ () => this.paypal() }>thanh toán</button>
                            </Link>
                    )
                } else {
                    nhat = (
                        <Link  style={buttonPaypal} to='/'>
                                <button className="text-uppercase" onClick={ () => this.paypal() }>thanh toán</button>
                            </Link>
                    )
                }
                
            } else {
                nhat = (
                    <Link  style={buttonPaypal} to='/paypal'>
                            <button className="text-uppercase" onClick={ () => this.paypal() }>thanh toán</button>
                        </Link>
                )
            } 
            
        } else {
            
            nhat = (
                <Link  style={buttonPaypal} to='/login'>
                        <button className="text-uppercase" onClick={ () => this.paypal() }>thanh toán</button>
                    </Link>
            )
        }
        
        var total = 0;
        var { allProduct } = this.state.productOfUser;
        if (allProduct) {
            var itemProduct = allProduct.map((val,index) => {
                let price = +val.price.split('.').join('').split('đ')[0];
                let totalPrice = price * +val.quantity;
                total += totalPrice;
                
                return(
                    <tr key={index}>
                        <td>
                            <figure><img src={val.img} alt="cây" /></figure>
                        </td>
                        <td className="text-uppercase">{ val.name }</td>
                        <td>{ val.price }</td>
                        <td>
                            <li>{ val.quantity }</li>
                        </td>
                        <td>{ totalPrice }đ</td>
                        <td><img src={require('./../../assets/images/Green Shop/delete.png')} alt="delete" onClick={ (event) => this.deleteProduct(event) } /></td>
                    </tr>
                )
            })
            
        }
        return (
            <Fragment>
                <main id="cart">
                    <div className="path"><span>Home / </span><span>Giỏ hàng</span></div>
                    <h4 className="text-uppercase">giỏ hàng</h4>
                    <div className="cart">
                        <table>
                            <thead>
                                <td className="text-center text-white text-uppercase">hình ảnh</td>
                                <td className="text-center text-white text-uppercase">tên sản phẩm</td>
                                <td className="text-center text-white text-uppercase">đơn giá</td>
                                <td className="text-center text-white text-uppercase">số lượng</td>
                                <td className="text-center text-white text-uppercase">thành tiền</td>
                                <td className="text-center text-white text-uppercase">xoá</td>
                            </thead>
                            <tbody>
                                { itemProduct }
                                
                            </tbody>
                        </table>
                    </div>
                    <div className="select-buy d-flex justify-content-end">
                        <button className="text-uppercase not-color" onClick={ () => this.removeCart() }>huỷ đơn hàng</button>
                        <Link to='/'><button className="text-uppercase" >tiếp tục mua</button></Link>
                    </div>
                    <div className="total-pay d-inline-block float-right">
                        <table className="d-flex flex-column">
                            <tbody>
                                <tr>
                                    <td>tổng tiền (chưa thuế)</td>
                                    <td>{  total}đ</td>
                                </tr>
                                <tr>
                                    <td>thuế (vat 10%)</td>
                                    <td>{ total / 10 }đ    </td>
                                </tr>
                            </tbody>
                            <thead>
                                <tr>
                                    <td>tổng phải thanh toán</td>
                                    <td className='totalPay'>{ total - (total / 10) } đ</td>
                                </tr>
                            </thead>
                        </table>
                    </div>
                    { nhat }
                </main>
            </Fragment>
        );
    }
}
const buttonPaypal = {
    display: 'block',
    clear: 'both',
    textAlign: 'right'
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        propsProductOfUser: (info,id) => {
            dispatch(allProductOfUser(info,id))
        },
        getUser : () => {
            dispatch(getUser())
        },
        updateUser : (info,id) => {
            dispatch(editUser(info,id))
        }
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
        productOfUser: state.allProductOfUser.productOfUser,
        allUser : state.allProductOfUser.allUser,
        errUpdate : state.signinReducer
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Index);