import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { actFetchUser, actRemoveUser, actRepairUser, editUser } from './../../../action/action';

class TableItem extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            form: null,
            userCanBuy : true
        }
        
    }
    componentWillMount() {
        this.props.actFetchUser();
    }
    shouldComponentUpdate() {
        return true;
    }
    componentDidUpdate(){
        this.props.actRepairUser({
            id: this.props.getInform.id ,
            name: this.props.getInform.hovaten,
            email: this.props.getInform.email,
            phone: this.props.getInform.phone,
            password: this.props.getInform.password,
        });
        this.props.actFetchUser();
    }
    render() {
        
        return (
            <Fragment>
                {this.tableItem(this.props.user)}
            </Fragment>
        );
    }
    Toggle = (value) => {
        let toggle = document.getElementsByClassName("config")[0];
        if (toggle.style.display === 'none') {
            toggle.style.display = 'block';
        } else {
            toggle.style.display = 'none';
        }
        this.props.config(value);
    }
    
    onRemove = value => {
        this.props.actRemoveUser(value);
    }
    // change status buy of user
    changeStatusBuyUser = (id,event) => {
        const { user } = this.props;
        let target = event.currentTarget;
        
        const userNeedFind = user.filter((val) => {
            return val.id == id;
        })
        if (target.innerText === 'Xong') {
            target.innerText = 'Chưa'
        } else {
            target.innerText = 'Xong'
        }
        this.setState({
            userCanBuy : !userNeedFind.statusPay
        })
        this.props.updateBuyUser({ statusPay : !userNeedFind[0].statusPay ,historyPay : null,totalPay : '0đ'},id)
        
    }
    

    tableItem = (user) => {
        let item = '';
        item = user.map((value, index) => {
            return (
                <tr key={index}>
                    <td>{index + 1}</td>
                    <td>{value.name}</td>
                    <td>{value.email}</td>
                    <td>{value.phone} </td>
                    <td>{value.password}</td>
                    <td><button className='btn bg-success' onClick={(event) => this.changeStatusBuyUser(value.id,event)}>{ value.statusPay === true ? 'Chưa' : 'Xong' }</button></td>
                    <td>
                        <button onClick={() => this.Toggle(value)} type="button" className="btn btn-success btn-table">Sửa<i className="fas fa-wrench"></i></button>
                        <button type="button" className="btn btn-warning btn-table" onClick={() => this.onRemove(value)} >Xóa<i className="fas fa-trash-alt"></i></button>
                    </td>
                </tr>
            );
        })
        return item;
    }
}
const mapStateToProps = state => {
    return {
        user: state.user
    }
}
const mapDispatchToProps = dispatch => {
    return {
        actFetchUser: () => {
            dispatch(actFetchUser());
        },
        actRemoveUser: (user) => {
            dispatch(actRemoveUser(user));
        },
        actRepairUser: user => {
            dispatch(actRepairUser(user));
        },
        updateBuyUser: (info,id) => {
            dispatch(editUser(info,id));
        }

    }
}
export default connect(mapStateToProps, mapDispatchToProps)(TableItem);
