import React, { Fragment } from 'react';
import { Bar, Line, Pie } from 'react-chartjs-2';
import Info from './info';
import { fecthChart, actFetchRequest, actFetchProduct, actFetchUser } from './../../../action/action';
import { connect } from 'react-redux';

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            chart: {
                user: '',
                order: '',
                product: '',
                request: ''
            }
        }
    }
    componentDidMount() {
        this.props.fecthChart();
        var day = new Date();
        var UpdateData = 30 - day.getDate();
        if (UpdateData <= 1) {
            this.props.actFetchRequest();
            this.props.actFetchProduct();
            this.props.actFetchUser();
        }
        // var now = new Date();
        // console.log(new Date(now.getFullYear(), now.getMonth() + 1, 0).getDate()) ;
    }

    componentWillReceiveProps(props) {
        if (props.chart.length > 0  ){
            this.setState({
                chart: {
                    user: props.chart[0].user,
                    order: props.chart[0].order,
                    product: props.chart[0].product,
                    request: props.chart[0].request
                }
            });
        }

    }
    render() {
        var { chart } = this.state;;
        var Chart = ({ chart, text, data }) => {
            switch (chart) {
                case "Bar":
                    return (
                        <>
                            <Bar data={data} options={{
                                title: {
                                    display: true,
                                    text: text,
                                    fontSize: 25,
                                    labels: {
                                        fontColor: 'white',
                                    }
                                },
                                legend: {
                                    display: true,
                                    position: 'right',
                                    labels: {
                                        fontColor: 'white',
                                    }
                                },
                                layout: {
                                    padding: {
                                        left: 10,
                                        right: 0,
                                        bottom: 0,
                                        top: 0,
                                    }
                                }

                            }} />
                        </>
                    )
                case "Pie":
                    return (
                        <>
                            <Pie data={data} options={{
                                title: {
                                    display: true,
                                    text: text,
                                    fontSize: 25,
                                    labels: {
                                        fontColor: 'white',
                                    }
                                },
                                legend: {
                                    display: true,
                                    position: 'right',
                                    labels: {
                                        fontColor: 'white',
                                    }
                                },
                                layout: {
                                    padding: {
                                        left: 10,
                                        right: 0,
                                        bottom: 0,
                                        top: 0,
                                    }
                                }
                            }} />
                        </>
                    )
                case "Line":
                    return (
                        <>
                            <Line data={data} options={{
                                title: {
                                    display: true,
                                    fontSize: 25,
                                    text: text,
                                    labels: {
                                        fontColor: 'white',
                                    }
                                },
                                legend: {
                                    display: true,
                                    position: 'right',
                                    labels: {
                                        fontColor: 'white',
                                    }
                                },
                                layout: {
                                    padding: {
                                        left: 10,
                                        right: 0,
                                        bottom: 0,
                                        top: 0,
                                    }
                                }
                            }} />
                        </>
                    )
                default:
                    return null;
            }
        }
        return (

            <Fragment>
                <div className="row layout">
                    <Info />
                </div>
                <div className="row">
                    <Chart chart="Bar" text="Biểu Đồ User" data={chart.user} />
                    <Chart chart="Pie" text="Biểu Đồ Order" data={chart.order} />
                    <Chart chart="Line" text="Biểu Đồ Product" data={chart.product} />
                    <Chart chart="Line" text="Biểu Đồ Request" data={chart.request} />
                </div>
            </Fragment>

        );
    }
}

export default connect(function (state) {
    return {
        chart: state.chart,
        user: state.user ,
        product : state.product,
        request : state.request
    }
}, function (dispatch) {
    return {
        fecthChart: () => {
            dispatch(fecthChart());
        },
        actFetchRequest: () => {
            dispatch(actFetchRequest());
        },
        actFetchProduct: () => {
            dispatch(actFetchProduct());
        },
        actFetchUser: () => {
            dispatch(actFetchUser());
        }
    }
})(Home);
