
import actionTypes from './../constant/actiontype';

const initialState = {
    hotProduct:[],
    productMore:[],
    promotion: [],
    newproduct: [],
    category : []
};

const product = ( state =  initialState , action)=>{
    switch( action.type ){
        case actionTypes.FETCH_PRODUCT :
            return {
                ...state, 
                hotProduct : action.Product[0].hotproduct ,
                productMore : action.Product[0].productmore ,
                promotion : action.Product[0].promotion , 
                newproduct : action.Product[0].newproduct , 
                category : action.Product[0].category    
            }
        case actionTypes.ACT_REPAIR_CATEGORY:
            return {...state , category : action.category}  
        case actionTypes.ACT_REMOVE_CATEGORY :
            return { ...state ,category : [...action.category ]}   
        default :
            return state;
    }
}

export default product;
