import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { actFetchProduct ,actRemoveCategory,actRepairCategory} from './../../../action/action';

class Tableitem extends React.Component {

    
    componentDidMount() {
        this.props.FetchProduct();
    }
    render() {
        return (
            <Fragment>
                {this.tableItem(this.props.category)}
            </Fragment>
        );
    }
    onRemove = value =>{
        var {category,match} = this.props;
        var item = '';
        for(var i=0; i < category.length;i++ ){
            if(parseInt(match.params.id) === category[i].id){
                item = category[i];
                for(var j =0; j < item.product.length;j++){
                    if( value.id === item.product[j].id ){
                        item.product.splice(j,1);
                    }
                }
                category[i] = item;
                break;
            }
        }
        this.props.actRemoveCategory(category);
    }
    tableItem = (category) => {
        let list = null;
        let item = '';
        var { match } = this.props;
        if(category !== null ){
            for(var i=0;i< category.length;i++  ){
                if( parseInt(match.params.id) === category[i].id ){
                    list = category[i];
                    item = list.product.map((value, index) => {
                        return (
                            <tr key={index}>
                                <td className="skeet">{index + 1}</td>
                                <td className="skeet">{value.name}</td>
                                <td ><img alt='ảnh' className="img_table" src={value.img} /></td>
                                <td className="skeet">{value.price_left}</td>
                                <td className="skeet">
                                    <button onClick={() => this.Toggle(value)} type="button" className="btn btn-success btn-table">Sửa<i className="fas fa-wrench"></i></button>
                                    <button onClick={ ()=>this.onRemove(value) } type="button" className="btn btn-warning btn-table">Xóa<i className="fas fa-trash-alt"></i></button>
                                </td>
                            </tr>
                        );
                    })    
                    break;
                }
            }
        }
        return item;
    }
    Toggle = (value) => {
        let toggle = document.getElementsByClassName("config")[0];
        if (toggle.style.display === 'none') {
            toggle.style.display = 'block';
        } else {
            toggle.style.display = 'none';
        }
        this.props.config(value);
    }
}
const mapStateToProps = state => {
    return {
        category: state.product.category
    }
}
const mapDispatchToProps = dispatch => {
    return {
        FetchProduct: () => {
            dispatch(actFetchProduct());
        },
        actRemoveCategory : category =>{
            dispatch(actRemoveCategory(category));
        },
        actRepairCategory : category =>{
            dispatch(actRepairCategory(category));
        }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Tableitem);
