import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { actFetchProduct ,actRemoveProductAdmin } from './../../../action/action';

class TableItem extends React.Component {

    

    componentWillMount() {
        this.props.FetchProduct();
    }

    render() {
        return (
            <Fragment>
                {this.tableItem(this.props.product)}
            </Fragment>
        );
    }
    
    Toggle = (value) => {
        let toggle = document.getElementsByClassName("config")[0];
        if (toggle.style.display === 'none') {
            toggle.style.display = 'block';
        } else {
            toggle.style.display = 'none';
        }
        this.props.config(value);
    }

    onRemove = value => {
        let {product} = this.props;
        for( var i=0;i<product.length;i++ ){
            if( parseInt(value.id) === parseInt(product[i].id) ){
                product.splice(i,1);
                break;
            }
        }
        this.props.actRemoveProductAdmin(product);
    }

    tableItem = (product) => {
        let item = '';
        item = product.map((value, index) => {
            return (
                <tr key={index}>
                    <td className="skeet">{index + 1}</td>
                    <td className="skeet">{value.name}</td>
                    <td ><img className="img_table" src={value.img} alt="txt" /></td>
                    <td className="skeet">{value.price_left}</td>
                    <td className="skeet">
                        <button onClick={() => this.Toggle(value)} type="button" className="btn btn-success btn-table">Sửa<i className="fas fa-wrench"></i></button>
                        <button onClick={()=>this.onRemove(value)} type="button" className="btn btn-warning btn-table">Xóa<i className="fas fa-trash-alt"></i></button>
                    </td>
                </tr>
            );
        })
        return item;
    }
}

const mapStateToProps = state => {
    return {
        product: state.product.newproduct
    }
}

const mapDispatchToProps = dispatch => {
    return {
        FetchProduct: () => {
            dispatch(actFetchProduct());
        },
        actRemoveProductAdmin : product=>{
            dispatch(actRemoveProductAdmin(product) );
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TableItem);
