import React, { Fragment } from 'react';
import { img, img_replace } from './../../../constant/menu';

class Config extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: '',
            tenCay: '',
            img: '',
            price: '',
            upload: {
                file: '',
                preview: ''
            }
        }
    }
    componentWillReceiveProps({ config }) {
        if (config.name !== this.state.tenCay) {
            this.setState({

                id: config.id,
                tenCay: config.name,
                img: config.img,
                price: config.price_left,

            });
        }
    }
    onChange = e => {
        var name = e.name;
        var value = e.value;
        this.setState({
            [name]: value
        });
        if ( e.files !== null ) {
            var file = e.files[0];
            var reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onloadend = () => {
                this.setState({
                    upload: {
                        file: file,
                        preview: reader.result
                    }
                });
            }
        }
    }
    onSubmit = e => {
        e.preventDefault();
        this.props.onSubmitValue(this.state);
    }
    render() {
        return (
            <Fragment>
                <div className="config table_product">
                    <div className="">
                        <form onSubmit={(e) => this.onSubmit(e)}>
                            <legend>Sửa Thông Tin</legend>
                            <div className="form-group">
                                <label>Tên Cây</label>
                                <input type="text" value={this.state.tenCay} onChange={(e) => this.onChange(e.target)} className="form-control" name="tenCay" />
                            </div>


                            <div className="form-group">

                                <div className="row">
                                    <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6 away_from">
                                        <label>Ảnh</label>
                                        <img alt='ảnh' className="form-control img_form"  src={this.state.img} />
                                    </div>
                                    <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6">
                                        <label>Thay Ảnh</label>
                                        <input type="file" onChange={(e) => this.onChange(e.target)} style={img} className="form-control img_form" />
                                        <img alt='ảnh' src={this.state.upload.preview} style={img_replace} />
                                    </div>
                                </div>
                            </div>
                            <div className="form-group">
                                <label>Giá </label>
                                <input type="text" onChange={(e) => this.onChange(e.target)} name="price" className="form-control" value={this.state.price} />
                            </div>
                            <button type="submit" className="btn btn-primary submit"> Lưu </button>
                        </form>
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default Config;
