import React from 'react';
import TableItem from './tableitem';
import Config from './config';

class User extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            config: '',
            form: {} ,
            success_config : false
        }
    }
    Toggle = () => {
        let toggle = document.getElementsByClassName("config")[0];
        if (toggle.style.display === 'none') {
            toggle.style.display = 'block';
        } else {
            toggle.style.display = 'none';
        }
    }
    config = value => {
        this.setState({
            config: value
        });
    }
    onSubmitValue = value => {
        this.setState({
            form: value ,
            success_config : true
        });
        this.Toggle();
    }
    render() {
        var message = this.state.success_config === true ? (<p style={{ margin:'0 auto'}}><i class="far fa-check-circle" style={{fontSize :'19px',fontWeight:'bold',color:'#eaa2a2' }}>Bạn vừa sửa thành công</i></p>):'';
        return (
            <>
                <div class="row text-center">
                    {message}
                </div>
                <div className="row text-center intro">
                    Danh Sách User
                </div>
                <table className="table">
                    <thead>
                        <tr>
                            <th>STT</th>
                            <th>Họ Và Tên</th>
                            <th>email</th>
                            <th>Phone</th>
                            <th>Mật Khẩu</th>
                            <th>Status</th>
                            <th>Công Cụ</th>
                        </tr>
                    </thead>
                    <tbody className="table-td">
                        <TableItem config={this.config} getInform={this.state.form} />
                    </tbody>
                </table>
                <Config config={this.state.config} onSubmitValue={this.onSubmitValue} />
            </>
        );
    }
}

export default User;
