import ActionType from './../constant/actiontype';

const initialState = [];

const user = (state = initialState, action) => {

    switch (action.type) {
        case ActionType.FETCH_REQUEST:
            state = action.request
            return state;
        case ActionType.ACT_SEND_REQUEST:
            state = action.request
            return state;
        case ActionType.ACT_REMOVE_REQUEST:
            for (var i = 0; i < state.length; i++) {
                if (action.request.id === state[i].id) {
                    state.splice(i,1);
                }
            }
            return [...state];
        default:
            return state;
    }

}

export default user;

