import React from 'react';
import TableItem from './tableitem';
import Config from './config';
import {actRepairProductAdmin} from './../../../action/action';
import {connect} from 'react-redux';

class User extends React.Component {

    constructor(props){
        super(props);
        this.state={
            config : '',
            form : {}
        }
    }

    Toggle = () => {
        let toggle = document.getElementsByClassName("config")[0];
        if (toggle.style.display === 'none') {
            toggle.style.display = 'block';
        } else {
            toggle.style.display = 'none';
        }
    }

    config = value =>{
        this.setState({
            config : value
        });
    }

    onSubmitValue = value=>{
        console.log(value);
        console.log(this.props.product);
        var {product} = this.props;
        for(var i=0; i < product.length;i++ ){
            if( parseInt(value.id) === parseInt(product[i].id)  ){
                product[i].name = value.tenCay;
                product[i].price_left = value.price;
                product[i].img = `/images/Green Shop/${value.upload.file.name}`;
                break;
            }
        }
        this.props.actRepairProductAdmin(product);
    }

    render() {
        return (
            <>
                <div className="row text-center intro">
                    Danh Sách Sản Phẩm
                </div>
                <table className="table">
                    <thead>
                        <tr>
                            <th>STT</th>
                            <th>Tên Cây</th>
                            <th>Ảnh</th>
                            <th>Giá</th>
                            <th>Công Cụ</th>
                        </tr>
                    </thead>
                    <tbody className="table-td">
                        <TableItem config={this.config} getInform={this.state.form} />
                    </tbody>
                </table>
                <Config config={this.state.config} onSubmitValue={this.onSubmitValue} />
            </>
        );
    }
}

const mapStateToProp = state =>{
    return{
        product : state.product.newproduct
    }
}

const mapDispatchToProps = dispatch =>{
    return{
        actRepairProductAdmin : product =>{
            dispatch(actRepairProductAdmin(product));
        }
    }
}

export default connect(mapStateToProp,mapDispatchToProps)(User);
