import React, { Fragment } from 'react';
import { infoItem } from './../../../constant/menu';
import { connect } from 'react-redux';
import { actFetchProduct, actFetchUser, actFetchRequest } from './../../../action/action';

class Info extends React.Component {

    componentDidMount() {
        this.props.actFetchProduct();
        this.props.actFetchUser();
        this.props.actFetchRequest();
    }
    render() {

        return (
            <>
                {this.displayItem()}
            </>
        );
    }
    displayItem = () => {
        var { product, user, request } = this.props;
        let item = '';
        let sum = 0;
        item = infoItem.map((value, index) => {
            switch (value.label) {
                case 'User':
                    for (var i = 0; i < user.length; i++) {
                        sum++;
                    }
                    return (
                        <Fragment key={index}>
                            <div className="item">
                                <div className="black">
                                    <div className="row">
                                        <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6" >
                                            <img src={value.img} alt="" />
                                        </div>
                                        <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6">
                                            <h5 className="name_info_1"> {value.label} <i className="fas fa-users-cog"></i></h5>
                                            <h5 className="number">{sum}</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Fragment>
                    );
                case 'Order':
                    sum = 0;
                    for ( i = 0; i < user.length; i++) {
                        if (user[i].allProduct !== null && user[i].allProduct.length > 0) {
                            sum++;
                        }
                    }
                    return (
                        <Fragment key={index}>
                            <div className="item">
                                <div className="black">
                                    <div className="row">
                                        <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6" >
                                            <img src={value.img} alt="" />
                                        </div>
                                        <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6">
                                            <h5 className="name_info_2"> {value.label} <i className="fas fa-users-cog"></i></h5>
                                            <h5 className="number">{sum}</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Fragment>
                    );
                case 'Product':
                    sum = 0;
                    for (i = 0; i < product.hotProduct.length; i++) {
                        sum++;
                    }
                    for ( i = 0; i < product.newproduct.length; i++) {
                        sum++;
                    }
                    for ( i = 0; i < product.productMore.length; i++) {
                        sum++;
                    }
                    for (i = 0; i < product.promotion.length; i++) {
                        sum++;
                    }
                    return (
                        <Fragment key={index}>
                            <div className="item">
                                <div className="black">
                                    <div className="row">
                                        <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6" >
                                            <img src={value.img} alt="" />
                                        </div>
                                        <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6">
                                            <h5 className="name_info_3"> {value.label} <i className="fas fa-users-cog"></i></h5>
                                            <h5 className="number">{sum}</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Fragment>
                    );
                case 'Category':
                    sum = 0;
                    for ( i = 0; i < product.category.length; i++) {
                        sum++;
                    }
                    return (
                        <Fragment key={index}>
                            <div className="item">
                                <div className="black">
                                    <div className="row">
                                        <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6" >
                                            <img src={value.img} alt="" />
                                        </div>
                                        <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6">
                                            <h5 className="name_info_4"> {value.label} <i className="fas fa-users-cog"></i></h5>
                                            <h5 className="number">{sum}</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Fragment>
                    );
                case 'Request':
                    sum = 0;
                    for (i = 0; i < request.length; i++) {
                        sum++;
                    }
                    return (
                        <Fragment key={index}>
                            <div className="item">
                                <div className="black">
                                    <div className="row">
                                        <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6" >
                                            <img src={value.img} alt="" />
                                        </div>
                                        <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6">
                                            <h5 className="name_info_5"> {value.label} <i className="fas fa-users-cog"></i></h5>
                                            <h5 className="number">{sum}</h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Fragment>
                    );
                default :
                    break;        
            }
            return true
        });
        return item;
    }
}

export default connect(function (state) {
    return {
        product: state.product,
        user: state.user,
        request: state.request
    }
}, function (dispatch) {
    return {
        actFetchProduct: () => {
            dispatch(actFetchProduct());
        },
        actFetchUser: () => {
            dispatch(actFetchUser());
        },
        actFetchRequest: () => {
            dispatch(actFetchRequest());
        }
    }
})(Info);
