import React, { Fragment } from 'react';

class Config extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: '',
            hovaten: '',
            email: '',
            dt: '',
            password: '',
            allProduct: ''
        }
    }
    componentWillReceiveProps({ config }) {
        if (config.id !== this.state.id) {
            this.setState({
                id: config.id,
                hovaten: config.name,
                email: config.email,
                dt: config.phone,
                password: config.password,
                allProduct: config.allProduct
            });
        }
    }
    render() {
        return (
            <Fragment>
                <div className="config order">
                    <div className="">
                        <h4>Danh Sách Order Của User</h4>
                        {this.displayItem()}

                    </div>
                </div>
            </Fragment>
        );
    }
    displayItem = () => {
        let item = '';
        var { allProduct } = this.state;
        console.log(allProduct);
        if (allProduct !== '') {
            item = allProduct.map((value, index) => {
                return (
                    <div key={index} className="row item_order">
                        <div className="col-xs-4 col-sm-4 col-md-4 col-lg-4">
                            <div className="item_order_img">
                                <img src={value.img} alt='ảnh' />
                            </div>
                        </div>
                        <div className="col-xs-8 col-sm-8 col-md-8 col-lg-8">
                            <h5>Tên : {value.name} </h5>
                            <h5>Giá : {value.price}</h5>
                            <h5>Số lượng : {value.quantity} </h5>
                        </div>
                    </div>
                );
            });
        }
        return item;
    }
}

export default Config;
