import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { actFetchRequest ,actRemoveRequest } from './../../../action/action';

class TableItem extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            form: null
        }
    }

    componentWillMount() {
        this.props.actFetchRequest();
    }

    componentWillReceiveProps(props) {
        this.setState({
            form: {
                id: props.getInform.id,
                name: props.getInform.hovaten,
                email: props.getInform.email,
                password: props.getInform.passwrod
            }
        });
    }
    
    shouldComponentUpdate() {
        return true;
    }

    componentDidUpdate(){
        // this.props.actRepairUser({
        //     id: this.props.getInform.id ,
        //     name: this.props.getInform.hovaten,
        //     email: this.props.getInform.email,
        //     phone: this.props.getInform.phone,
        //     password: this.props.getInform.password,
        // });
    }

    render() {
        return (
            <Fragment>
                {this.tableItem(this.props.request)}
            </Fragment>
        );
    }
    
    Toggle = (value) => {
        let toggle = document.getElementsByClassName("config")[0];
        if (toggle.style.display === 'none') {
            toggle.style.display = 'block';
        } else {
            toggle.style.display = 'none';
        }
        this.props.config(value);
    }
    onRemove = value => {
        this.props.actRemoveRequest(value);
    }

    tableItem = (request) => {
        let item = '';
        item = request.map((value, index) => {
            return (
                <tr key={index}>
                    <td>{index + 1}</td>
                    <td>{value.hovaten}</td>
                    <td>{value.email}</td>
                    <td>{value.dt} </td>
                    <td>{value.comment}</td>
                    <td>
                        <button onClick={() => this.Toggle(value)} type="button" className="btn btn-success btn-table">Xem<i className="fas fa-wrench"></i></button>
                        <button type="button" className="btn btn-warning btn-table" onClick={() => this.onRemove(value)} >Xóa<i className="fas fa-trash-alt"></i></button>
                    </td>
                </tr>
            );
        })
        return item;
    }
}
const mapStateToProps = state => {
    return {
        request: state.request
    }
}
const mapDispatchToProps = dispatch => {
    return {
        actFetchRequest : ()=>{
            dispatch( actFetchRequest() );
        },
        actRemoveRequest : (value)=>{
            dispatch( actRemoveRequest(value) );
        },
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TableItem);
