import actionTypes  from '../constant/actiontype';

const stateProductUser = {
    productOfUser : []
}

const allProductOfUser = (state = stateProductUser, action) => {
    switch(action.type) {
        case actionTypes.ALL_PRODUCT_OF_USER:
            
            state = {
                ...state,
                productOfUser : action.allProductOfUser
            }
            break;
        case actionTypes.GET_USER:
            
            const userCurrent = JSON.parse(localStorage.getItem('profileUser'))
            if (userCurrent) {
                
                const selectUser = action.allUser.filter((val) => {
                    return val.name === userCurrent.name
                })
                if (selectUser[0].allProduct) {
                    localStorage.setItem('productOfUser',JSON.stringify(selectUser[0].allProduct))
                    localStorage.setItem('profileUser',JSON.stringify(selectUser[0]))
                }
            }
            return state
            case actionTypes.EDIT_USER:
                return state    
        default:
            break    
    }
    return state; 
    
}
export default allProductOfUser