import React from 'react';
import TableItem from './tableitem';
import Config from './config';

class ListOrder extends React.Component {

    constructor(props){
        super(props);
        this.state={
            config : '',
            form : {}
        }
    }
    Toggle = () => {
        let toggle = document.getElementsByClassName("config")[0];
        if (toggle.style.display === 'none') {
            toggle.style.display = 'block';
        } else {
            toggle.style.display = 'none';
        }
    }
    config = value =>{
        this.setState({
            config : value
        });
    }
    onSubmitValue = value=>{
        this.setState({
            form : value
        });
    }
    render() {
        return (
            <>
                <div className="row text-center intro">
                    Danh Sách Order Sản Phẩm
                </div>
                <table className="table">
                    <thead>
                        <tr>
                            <th>STT</th>
                            <th>Họ Và Tên</th>
                            <th>email</th>
                            <th>Phone</th>
                            <th>Số Sản Phẩm Order</th>
                            <th>Công Cụ</th>
                        </tr>
                    </thead>
                    <tbody className="table-td">
                        <TableItem config={this.config} getInform={this.state.form} />
                    </tbody>
                </table>
                <Config config={this.state.config} onSubmitValue={this.onSubmitValue} />
            </>
        );
    }
}

export default ListOrder;
