import React, { Fragment } from 'react';
import { connect } from 'react-redux';
// import { Link } from 'react-router-dom';
class Item extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            filterName: ''

        }
    }
    componentWillMount() {
        if (this.props.product !== null) {
            this.setState({
                filterName: this.props.product
            });
        }
    }
    componentWillReceiveProps(nextProps){
        if( nextProps.newproduct ){
            this.setState({
                filterName:{
                    product : nextProps.newproduct
                }
            });
        }
    }   
    // url to detailproduct from list-product
    // urlForProduct = (e) => {
    //     const name = e.currentTarget.innerText;
    //     const img = e.currentTarget.parentElement.parentElement.previousSibling.children[0].getAttribute('src');
    //     const price_left = e.currentTarget.parentElement.parentElement.children[2].children[0].innerText;
    //     const price_right = e.currentTarget.parentElement.parentElement.children[2].children[1].innerText;
    //     let infoDetail = {
    //         name,
    //         img,
    //         price_left,
    //         price_right,
    //     }
    //     localStorage.setItem('infoDetail',JSON.stringify(infoDetail))
    // }
    render() {
        return (
            <Fragment>
                {this.productItem(this.state.filterName)}
            </Fragment>
        );
    }
    productItem = (product) => {
        let item = '';
        if (product) {
            item = product.product.map((value, index) => {
                return (
                    
                        <div key={index} className="product position-relative">
                        <figure><img src={value.img} alt="hoa" /></figure>
                        <div className="content">
                            <p  >{value.name}</p>
                            <img src={require('./../../assets/images/Green Shop/star.png')} alt="star" />
                            <div className="price">
                                <div className="price--new">{value.price_left}.000đ</div>
                                <div className="price--old">{value.price_right}.000đ</div>
                            </div>
                        </div>
                    </div>
                    
                );
            });
        }
        return item;
    }
}

export default connect(state=>({newproduct : state.product.newproduct}))(Item);
