import React, { Fragment } from 'react';
import {stylearea} from './../../../constant/menu';

class Config extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id : '',
            comment : ''
        }
    }
    componentWillReceiveProps({ config }) {
        if (config.comment !== this.state.comment) {
            this.setState({
                id : config.id ,
                comment : config.comment
            });
        }
    }
    render() {
        return (
            <Fragment>
                <div className="config">
                    <div className="">
                        <form onSubmit={(e)=>this.onSubmit(e)}>
                            <legend>Xem Request</legend>
                            <div className="form-group">
                                <textarea row="3" col="4" style={stylearea} value={this.state.comment} disabled className="form-control" ></textarea>
                            </div>
                        </form>
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default Config;
