import React, { Fragment } from 'react';
import { connect } from 'react-redux';
// import { actRepairCategory } from './../../../action/action';

class Config extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            tenCay: '',
            price: '',
        }
    }
    componentWillReceiveProps({ config }) {
        if(config) {
            if( this.state.tenCay !== config.name || this.state.price !== config.price_left ) {
                this.setState({
                    tenCay: config.name,
                    price: config.price_left
                });
            }

        }
    }
    onChange = e => {
        this.setState({
            [e.name]: e.value
        });
    }
    onSubmit = e => {
        e.preventDefault();
        var { match, category, config } = this.props;
        var { tenCay, price } = this.state;
        var item = null;
        if (category.length > 0) {
            for (var i = 0; i < category.length; i++) {
                if (parseInt(match.params.id) === category[i].id) {
                    item = category[i];
                    for (var j = 0; j < item.product.length; j++) {
                        if (config.id === item.product[j].id) {
                            item.product[j].name = tenCay;
                            item.product[j].price_left = price;
                        }
                    }
                    category[i] = item;
                }
            }
        }
        console.log(category);
        this.props.getInform(category);
    }
    render() {
        return (
            <Fragment>
                <div className="config">
                    <div>
                        <form onSubmit={(e) => this.onSubmit(e)}>
                            <legend>Sửa Thông Tin</legend>

                            <div className="form-group">
                                <label>Tên Cây</label>
                                <input type="text" value={this.state.tenCay} onChange={(e) => this.onChange(e.target)} className="form-control" name="tenCay" />
                            </div>
                            <div className="form-group">
                                <label>Giá</label>
                                <input type="text" value={this.state.price} onChange={(e) => this.onChange(e.target)} className="form-control" name="price" />
                            </div>
                            <button type="submit" className="btn btn-primary submit"> Lưu </button>
                        </form>
                    </div>
                </div>
            </Fragment>
        );
    }
}

const mapStateToProps = state => {
    return {
        category: state.product.category
    }
}

const mapDispatchToProps = dispatch => {
    return {
        // actRepairCategory: category => {
        //     dispatch(actRepairCategory(category));
        // }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Config);
