import React, { Fragment } from 'react';

class Config extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id : '',
            hovaten: '',
            email: '',
            dt: '',
            password: ''
        }
    }
    componentWillReceiveProps({ config }) {
        if (config.name !== this.state.hovaten) {
            this.setState({
                id : config.id ,
                hovaten: config.name ,
                email: config.email,
                dt: config.phone,
                password: config.password
            });
        }
    }
    onChange = e=>{
        var name = e.name;
        var value = e.value;
        this.setState({
            [name]: value
        });
    }
    onSubmit= e =>{
        e.preventDefault();
        this.props.onSubmitValue(this.state);
        
    }
    render() {
        return (
            <Fragment>
                <div className="config">
                    <div className="">
                        <form onSubmit={(e)=>this.onSubmit(e)}>
                            <legend>Sửa Thông Tin</legend>
                            <div className="form-group">
                                <label>Họ Và Tên</label>
                                <input type="text" value={this.state.hovaten} onChange={(e)=>this.onChange(e.target)} className="form-control" name="hovaten" />
                            </div>
                            <div className="form-group">
                                <label>Email</label>
                                <input type="text" value={this.state.email} onChange={(e)=>this.onChange(e.target)} className="form-control"  name="email" />
                            </div>
                            <div className="form-group">
                                <label>Số ĐT</label>
                                <input type="text" onChange={(e)=>this.onChange(e.target)} name="dt" className="form-control" value={ this.state.dt } />
                            </div>
                            <div className="form-group">
                                <label>Mật Khẩu</label>
                                <input type="text" onChange={(e)=>this.onChange(e.target)} name="password" className="form-control" value={this.state.password} />
                            </div>
                            <button type="submit" className="btn btn-primary submit"> Lưu </button>
                        </form>
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default Config;
