import React from 'react';
import TableItem from './tableitem';
import Config from './config';
import {connect} from 'react-redux';
import { actRepairCategory} from './../../../action/action';

class User extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            config : '',
            getform : ''
        }
    }
    config=value=>{
        this.setState({
            config : value
        });
    }
    getInform = value =>{
        this.setState({
            getform : value
        });
        this.props.actRepairCategory(value);
    }
    render() {
        var {match} = this.props;
        return (
            <>
                <div className="row text-center intro">
                    Danh Sách Category
                </div>

                <table className="table">
                    <thead>
                        <tr>
                            <th>STT</th>
                            <th>Thuộc Loại Cây</th>
                            <th>Cây</th>
                            <th>Giá</th>
                            <th>Công Cụ</th>
                        </tr>
                    </thead>
                    <tbody className="table-td">
                        
                        <TableItem match={match} config={ this.config } onInform={this.state.getform} />
                        
                    </tbody>
                </table>
                <Config config = { this.state.config }  match={ match } getInform={this.getInform} />
            </>
        );
    }
}
const mapDispatchToProps = dispatch => {
    return {
        actRepairCategory : category =>{
            dispatch(actRepairCategory(category));
        }
    }
}
export default connect(null, mapDispatchToProps)(User);
