import actionTypes from './../constant/actiontype';

const initialState = [];

const user = ( state =  initialState , action)=>{

    switch( action.type ){
        case actionTypes.FETCH_USER:
            state = action.user;
            return state;
        case actionTypes.ACT_REMOVE_USER:
            for(var i=0 ;i < state.length;i++){
                if( action.user.id === state[i].id ){
                    state.splice(i,1);
                }
            }
            return [...state];
        case actionTypes.ACT_REPAIR_USER :
            for(var i=0;i<state.length;i++){
                if(action.user.id === state[i].id ){
                    state[i] = action.user;
                }
            }
            return [...state]
        default :
            return state;
    }

}

export default user;

