import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import { actFetchUser, actRemoveOrder } from './../../../action/action';

class TableItem extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            form: null
        }

    }

    componentWillMount() {
        this.props.actFetchUser();
    }

    componentWillReceiveProps(props) {
        this.setState({
            form: {
                id: props.getInform.id,
                name: props.getInform.hovaten,
                email: props.getInform.email,
                password: props.getInform.passwrod
            }
        });
    }

    shouldComponentUpdate() {
        return true;
    }

    render() {
        return (
            <Fragment>
                {this.tableItem(this.props.user)}
            </Fragment>
        );
    }

    Toggle = (value) => {
        let toggle = document.getElementsByClassName("config")[0];
        if (toggle.style.display === 'none') {
            toggle.style.display = 'block';
        } else {
            toggle.style.display = 'none';
        }
        this.props.config(value);
    }

    onRemove = value => {
        this.props.actRemoveOrder({
            "id" : value.id,
            "name" : value.name,
            "phone": value.phone,
            "password" : value.password,
            "repassword" : value.repassword,
            "website":value.website,
            "allProduct" : null
        });
    }

    tableItem = (user) => {
        let item = '';
        item = user.map((value, index) => {
            if ( value.allProduct != null && value.allProduct.length >0 ) {
                var sum = 0;
                for(var i=0;i<value.allProduct.length;i++){
                    sum++;
                }
                return (
                    <tr key={index}>
                        <td>{index + 1}</td>
                        <td>{value.name}</td>
                        <td>{value.email}</td>
                        <td>{value.phone} </td>
                        <td>{ sum }</td>
                        <td>
                            <button onClick={() => this.Toggle(value)} type="button" className="btn btn-success btn-table">Xem<i className="fas fa-wrench"></i></button>
                            <button type="button" className="btn btn-warning btn-table" onClick={() => this.onRemove(value)} >Xóa<i className="fas fa-trash-alt"></i></button>
                        </td>
                    </tr>
                );
            }
            return true
        })
        return item;
    }
}
const mapStateToProps = state => {
    return {
        user: state.user
    }
}
const mapDispatchToProps = dispatch => {
    return {
        actFetchUser: () => {
            dispatch(actFetchUser());
        },
        actRemoveOrder: (order) => {
            dispatch(actRemoveOrder(order));
        }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(TableItem);
