
import actionTypes from './../constant/actiontype';

const initialState = {
    allUser : ''
};

const product = ( state =  initialState , action)=>{
    switch( action.type ){
        case actionTypes.GET_USER :
            state = {
                ...state,
                allUser : action.allUser
            }
            return state
        default :
            return state;
    }
}

export default product;
